import React,{ Component }  from 'react';
import { Layout,Button, Menu} from 'antd';
import './App.css';
import {BrowserRouter as Router, Switch, Route, Link} from 'react-router-dom';
import MainCardShentai from '././pages/main/mainCardShentai'
import DaoHang from '././pages/main/DaoHang';
import Maincard1 from '././pages/main/maincard1';
import LGairdrop from "././pages/main/LGairdrop";
import Home from "./pages/Home";
import Genxin from './pages/Genxin';
import KnowledgeBase from './pages/KnowledgeBase';
import Warning from './pages/warning';
import Hongbao from './pages/ST/SThongbao';
import STAirdrop from './pages/ST/STAirdrop';
const { SubMenu } = Menu;
const { Footer, Content } = Layout;



class App extends Component {
  state = {
    classSelect:"",

  };

  async componentDidMount(){

  

    var system ={}; 
    var p = navigator.platform;      
    system.win = p.indexOf("Win") == 0; 
    system.mac = p.indexOf("Mac") == 0; 
    system.x11 = (p == "X11") || (p.indexOf("Linux") == 0);    
    if(system.win||system.mac||system.xll){
      //如果是电脑,添加ui container
        this.setState({classSelect:"ui container"})
    }else{  //如果是手机,不添加container
      this.setState({classSelect:"0"}); 
    }


  }
 
  render(){
    
  return (

    <div  className={this.state.classSelect}>
      <Layout>
          <Content className="site-layout" style={{ padding: '0 2px', marginTop: 2 }}>
              <div className="site-layout-background" style={{ padding: 1, minHeight: 690 }}>
                  <Router>
                      <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['1']}>
                          <Menu.Item key="1"><Link to="/">主页</Link></Menu.Item>
                          <SubMenu key="SubMenu"  title="生态">
                          <Menu.ItemGroup >
                              < Menu.Item key="setting:1"><Link to="/STAirdrop">天女号空投机</Link></Menu.Item>
                              < Menu.Item key="setting:2"><Link to="/hongbao">发红包</Link></Menu.Item>
                          </Menu.ItemGroup >
                          </SubMenu>
                          <Menu.Item key="3"><Link to="/KnowledgeBase">入口</Link></Menu.Item>
                          <Menu.Item key="4"><Link to="/warning">免责声明</Link></Menu.Item>
                          <Menu.Item key="5"><Link to="/Genxin">更新</Link> </Menu.Item>
                      </Menu>
                  
                      <Switch>
                          <Route path="/STAirdrop" component={STAirdrop} />
                          <Route path="/hongbao" component={Hongbao} />
                          <Route path="/KnowledgeBase" component={KnowledgeBase} />
                          <Route path="/warning" component={Warning} />
                          <Route path="/Genxin" component={Genxin} />
                          <Route path="/" exact component={Home} /> 
                      </Switch>
                  </Router>
              </div>
              <Footer style={{ textAlign: 'center', padding:'5px 50px'}}>LG网站  lg-qkl.xyz</Footer>
          </Content>
      </Layout>
   </div>
    
  );
}
}

export default App;
